<template>
	<article>
		<BaseCard :with-media="true">
			<template slot="media">
				<img
					:src="
						data.thumbnail ||
							'data:image/gif;base64,R0lGODlhAQABAIAAAMLCwgAAACH5BAAAAAAALAAAAAABAAEAAAICRAEAOw=='
					"
					:alt="'Thumbnail for ~' + data.title + '~'"
				/>
			</template>
			<template slot="head">
				<h2 class="title">{{ data.title || "Titel" }}</h2>
				<div class="tags">
					<img class="icon" src="@/assets/icon-label.svg" />
					<span
						v-for="(tag, index) in (data.tags || []).slice(
							0,
							this.$config.card.displayedTags
						)"
						:key="`${index}-${tag}`"
					>
						{{ tag }},
					</span>
				</div>
			</template>
			<template slot="content">
				{{ (data.description || "Beschreibung...").substring(0, 300) }}
				{{ (data.description || "").length > 300 ? "..." : "" }}
			</template>
			<template slot="footer">
				<div class="button_wrapper">
					<div class="provider-name">{{ data.providerName }}</div>
					<div>
						<BaseButton
							v-if="data._id"
							:secondary="true"
							@ButtonClicked="dialog.active = true"
							>{{ $lang.buttons.open }}
						</BaseButton>
						<RouterLink
							v-if="readOnly != true"
							:to="{ path: '/edit/' + data._id }"
						>
							<BaseButton :secondary="true">{{
								$lang.buttons.edit
							}}</BaseButton>
						</RouterLink>
					</div>
				</div>
			</template>
		</BaseCard>
		<ConfirmDialog :config="dialog" @confirm="onConfirm" />
	</article>
</template>

<script>
/* load confirmDialog async */
const ConfirmDialog = () =>
	import(/* webpackChunkName: "confirmDialog" */ "@/components/dialogs/confirm.vue");
import BaseCard from "@/components/base/BaseCard.vue";
import BaseButton from "@/components/base/BaseButton.vue";

export default {
	name: "ContentForm",
	components: {
		ConfirmDialog,
		BaseCard,
		BaseButton,
	},
	props: {
		data: {
			type: Object,
			required: true,
		},
		readOnly: {
			type: Boolean,
			default: false,
		},
	},
	data() {
		return {
			dialog: {
				active: false,
				title: this.$lang.contentCard.dialog.title,
				content: this.$lang.contentCard.dialog.content,
				confirm: this.$lang.contentCard.dialog.confirm,
				cancle: this.$lang.contentCard.dialog.cancle,
			},
		};
	},
	methods: {
		onConfirm() {
			window.open(
				this.$config.API.serverServerUrl +
					this.$config.API.viewContentPath +
					this.data._id,
				"_blank"
			);
		},
	},
};
</script>

<style lang="scss" scoped>
span {
	vertical-align: middle;
}
.tags {
	color: #999;
}
.button_wrapper {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
</style>
